.header {
  width: 100%;
  height: 6rem;
  background-color: #FAFAFA;
  box-shadow: 0 0 1em 0 #D7D7D7;
  background-position: right;
  background-repeat: no-repeat;
  padding: 1rem;
  display: flex;
  background-size: 40%;
}

.headerMobile {
  display: block;
}

.li-Nav {
  margin-top: 1.5rem;
}

.ul-Nav {
  position: absolute;
}

.logo {
  width: 4rem;
  height: 4rem;
  margin-left: 5rem;
  z-index: 10;
}

.navPosition {
  position: absolute !important;
  margin-top: 1rem;
}

.linkNav {
    background-color: white;
    border-bottom: 1px solid #D7D7D7;
}

.navbar {
  padding: 0 !important;
  width: 90vw;
  text-align: center;
  z-index: 9 !important;
}

.headerDesk {
  display: none;
}

.liStyle {
  display: flex;
  margin-bottom: 0 !important;
}

.liStyle li {
  list-style-type: none; 
}

.liMob {
  margin-right: 2rem;
}

.navDesk {
  display: flex;
  align-items: center;
}

.textHeader {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding:  1rem;
  font-size: 9px;
  width: 100%;
  color: #838383;
}

.imgbrochazo {
  display: none;
}

@media only screen and (min-width: 600px) {
  .headerDesk {
    display: flex;
    width: auto;
    margin-left: -17px;
    margin-top: 16px;
    height: 30px;
    border-bottom-right-radius: 80px;
    -webkit-box-shadow: 0px 8px 25px -10px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 8px 25px -10px rgba(0,0,0,0.75);
    box-shadow: 0px 8px 25px -10px rgba(0,0,0,0.75);
    z-index: 1;
  }

  .textHeader {
    display: flex;
    align-items: flex-start;
    width: 80% !important;
  }

  .header {
    width: 100%;
    height: 6rem;
    background-color: #FAFAFA;
    box-shadow: 0 0 1em 0 #D7D7D7;
    padding: 1rem;
    display: flex;
    display: flex;
    flex-wrap: wrap;
  }

  .headerMobile {
    display: none;
  }

  .logo {
    width: 4rem;
    height: 4rem;
    margin-left: 0rem;
  }
}
